import React, {useState} from 'react';
import Header from './header';
import Popup from 'reactjs-popup';
import 'reactjs-popup/dist/index.css';

function ViewPatientDetails() {
    const [showBasicInformation, setShowBasicInformation] = useState(false);
    const [showLastMedicationDetails, setShowLastMedicationDetails] = useState(false);
    const [showAddNewVaccinationDetails, setAddNewVaccinationDetails] = useState(false);
    const [showAlergiesDetails, setShowAlergiesDetails] = useState(false);
    const [showMedicationHistory, setShowMedicationHistory] = useState(false);
    const [showChronicDiseasesCondition, setShowChronicDiseasesCondition] = useState(false);

    const onOpenAddNewVaccinationDetailsModel = () => setAddNewVaccinationDetails(true);
    const onCloseAddnNewVaccinationDetailsModel = () => setAddNewVaccinationDetails(false);

    const onOpenLastMedicationDetailsModel = () => setShowLastMedicationDetails(true);
    const onCloseLastMedicationDetailsModel = () => setShowLastMedicationDetails(false);

    const onOpenModal = () => setShowBasicInformation(true);
    const onCloseModal = () => setShowBasicInformation(false);

    const onOpenAlergiesDetailsModel = () => setShowAlergiesDetails(true);
    const onCloseAlergiesDetailsModel = () => setShowAlergiesDetails(false);

    const onOpenShowMedicationHistory = () => setShowMedicationHistory(true);
    const onCloseShowMedicationHistory = () => setShowMedicationHistory(false);

    const onOpenShowChronicDiseasesCondition = () => setShowAlergiesDetails(true);
    const onCloseShowChronicDiseasesCondition = () => setShowAlergiesDetails(false);

    const MedicationHistoryModel = () => (
        <Popup open={showMedicationHistory}
            position="bottom">
            <div className='container model-radius mt-5'>
                <div className='row model-close-button'>
                    <i class="fa fa-times-circle"
                        onClick={
                            () => onCloseShowMedicationHistory()
                        }
                        aria-hidden="true"></i>
                </div>
                <div className='card patient-card-popup'>
                    <div className='row m-2'>
                        <h4>
                            Medication History
                        </h4>
                        <div className='row'>
                            <div className="card col-sm-5 m-2">
                                <div className="row">
                                    01/02/2022
                                </div>
                                <div className="row">
                                    Note
                                </div>
                                <div className="row">
                                    Comment
                                </div>
                                <br/>
                                <br/>
                                <br/>
                            </div>
                            <div className="card col-sm-5 m-2">
                                <div className="row">
                                    01/02/2022
                                </div>
                                <div className="row">
                                    Note
                                </div>
                                <div className="row">
                                    Comment
                                </div>
                                <br/>
                                <br/>
                                <br/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Popup>
    );

    const AlergiesDetailsModelPopupModel = () => (
        <Popup open={showAlergiesDetails}
            position="bottom">
            <div className='container model-radius mt-5'>
                <div className='row model-close-button'>
                    <i class="fa fa-times-circle"
                        onClick={
                            () => onCloseAlergiesDetailsModel()
                        }
                        aria-hidden="true"></i>
                </div>
                <div className='card patient-card-popup'>
                    <div className='row m-2'>
                        <h4>
                            Add New Allergic Details
                        </h4>
                        <div className='row'>
                            <div className="col-sm-7"></div>
                            <div className="col-sm-5">
                                <div class="form-group">
                                    <small className='text-bold'>Date</small>
                                    <input class="form-control" type="date" placeholder="Alex B." required/>
                                </div>
                            </div>
                        </div>
                        <div className='row'>
                            <div class="form-group">
                                <label for="exampleFormControlTextarea1">Example textarea</label>
                                <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Popup>
    );

    const PopupExample = () => (
        <Popup open={showBasicInformation}
            position="bottom">
            <div className='container model-radius mt-5'>
                <div className='row model-close-button'>
                    <i class="fa fa-times-circle"
                        onClick={
                            () => onCloseModal()
                        }
                        aria-hidden="true"></i>
                </div>
                <div className='card patient-card-popup'>
                    <div className='row p-2'>
                        <h4>
                            Patient Information
                        </h4>
                        <div className='row'>
                            <div className="col-sm-3"></div>
                            <div className="col-sm-9">
                                <div className="row">
                                    <div className="col-sm-4">
                                        <div class="form-group">
                                            <small className='text-bold'>First Name</small>
                                            <input class="form-control" type="text" placeholder="Alex B." required/>
                                        </div>
                                    </div>
                                    <div className="col-sm-4">
                                        <div class="form-group">
                                            <small className='text-bold'>Middle Name</small>
                                            <input class="form-control" type="text" placeholder="Alex B." readonly/>
                                        </div>
                                    </div>
                                    <div className="col-sm-4">
                                        <div class="form-group">
                                            <small className='text-bold'>Last Name</small>
                                            <input class="form-control" type="text" placeholder="Alex B." readonly/>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-4">
                                        <div class="form-group">
                                            <small className='text-bold'>Date Of Birth</small>
                                            <input class="form-control" type="text" placeholder="Alex B." required/>
                                        </div>
                                    </div>
                                    <div className="col-sm-4">
                                        <div class="form-group">
                                            <small className='text-bold'>Gender</small>
                                            <input class="form-control" type="text" placeholder="Alex B." readonly/>
                                        </div>
                                    </div>
                                    <div className="col-sm-4">
                                        <div class="form-group">
                                            <small className='text-bold'>Merital Status</small>
                                            <input class="form-control" type="text" placeholder="Alex B." readonly/>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-8">
                                        <div class="form-group">
                                            <small className='text-bold'>NIC</small>
                                            <input class="form-control" type="text" placeholder="Alex B." required/>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div class="form-group">
                                        <small className='text-bold'>Address</small>
                                        <input class="form-control" type="text" placeholder="Alex B." required/>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-5">
                                        <div class="form-group">
                                            <small className='text-bold'>Address</small>
                                            <input class="form-control" type="text" placeholder="Alex B." required/>
                                        </div>
                                    </div>
                                    <div className="col-sm-5">
                                        <div class="form-group">
                                            <small className='text-bold'>Address</small>
                                            <input class="form-control" type="text" placeholder="Alex B." required/>
                                        </div>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-sm-5">
                                        <div class="form-group">
                                            <small className='text-bold'>Address</small>
                                            <input class="form-control" type="text" placeholder="Alex B." required/>
                                        </div>
                                    </div>
                                    <div className="col-sm-5">
                                        <div class="form-group">
                                            <small className='text-bold'>Address</small>
                                            <input class="form-control" type="text" placeholder="Alex B." required/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </Popup>
    );

    const LastMedicationModel = () => (
        <Popup open={showLastMedicationDetails}
            position="bottom">
            <div className='container model-radius'>
                <div className='row model-close-button'>
                    <i class="fa fa-times-circle"
                        onClick={
                            () => onCloseLastMedicationDetailsModel()
                        }
                        aria-hidden="true"></i>
                </div>
                <div className='card patient-card-popup'>
                    <div className='row p-2'>
                        <h4>
                            Last Prescription Information
                        </h4>
                        <div className='row'>
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Doctor Name</small>
                                    <input class="form-control" type="text" placeholder="Dr. Alex B." required/>
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Date</small>
                                    <input class="form-control" type="date" placeholder="Alex B." required/>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-10">
                                <div class="form-group">
                                    <small className='text-bold'>Note*</small>
                                    <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-12">
                                <div class="form-group">
                                    <small className='text-bold'>Comment</small>
                                    <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Popup>
    );

    const AddNewVaccinationModel = () => (
        <Popup open={showAddNewVaccinationDetails}
            position="bottom">
            <div className='container model-radius'>
                <div className='row model-close-button'>
                    <i class="fa fa-times-circle"
                        onClick={
                            () => onCloseAddnNewVaccinationDetailsModel()
                        }
                        aria-hidden="true"></i>
                </div>
                <div className='card patient-card-popup'>
                    <div className='row p-2'>
                        <h4>
                            Add New Vaccination
                        </h4>
                        <div className="row">
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Name of the Vaccine</small>
                                    <input class="form-control" type="text" placeholder="Alex B." required/>
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Date</small>
                                    <input class="form-control" type="date" placeholder="Alex B." required/>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Place of vaccination</small>
                                    <input class="form-control" type="text" placeholder="Alex B." required/>
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Batch Number</small>
                                    <input class="form-control" type="text" placeholder="Alex B." required/>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Dose</small>
                                    <input class="form-control" type="number" placeholder="" required/>
                                </div>
                            </div>
                            <div className="col-sm-6">
                                <div class="form-group">
                                    <small className='text-bold'>Time</small>
                                    <input class="form-control" type="time" placeholder="Alex B." required/>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-12">
                                <div class="form-group">
                                    <small className='text-bold'>Comment</small>
                                    <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-6 m-3"></div>
                            <button type="button" class="btn btn-secondary">Secondary</button>
                        </div>
                    </div>
                </div>
            </div>
        </Popup>
    );

    return (
        <div className=' container-fluid view-patient-details-body'>
            <Header></Header>

            <div className='row mt-5'>
                <div className='col-sm-12 mt-5'>
                    {/* Start First Row */}
                    <div className='row mt-2'>
                        <PopupExample className='mt-5'/>
                        <LastMedicationModel className='mt-5'/>
                        <AddNewVaccinationModel/>
                        <AlergiesDetailsModelPopupModel/>
                        <MedicationHistoryModel/>
                        <div className='col-sm-4'>
                            <div className='card patient-card'>
                                <div className='row p-2'>
                                    <h4>
                                        Basic Information
                                    </h4>
                                    <div class="form-group">
                                        <small className='text-bold'>NIC</small>
                                        <input class="form-control" type="text" placeholder="922212661V" readonly/>
                                    </div>
                                    <div class="form-group">
                                        <small className='text-bold'>Name</small>
                                        <input class="form-control" type="text" placeholder="Alex B." readonly/>
                                    </div>
                                    <div class="form-group">
                                        <small className='text-bold'>Age</small>
                                        <input class="form-control" type="text" placeholder="Alex " readonly/>
                                    </div>
                                    <div className='vpd-more-details'>
                                        <p className=' text-bold text-right vpd-more-details-text'
                                            onClick={
                                                () => onOpenModal()
                                        }>More Details
                                            <i class="p-2 fa fa-caret-right" aria-hidden="true"></i>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='col-sm-4'>
                            <div className='card patient-card'>
                                <div className='row p-2'>
                                    <h4>
                                        Last Medication Details
                                    </h4>
                                    <div class="form-group">
                                        <small className='text-bold'>Doctor Name</small>
                                        <input class="form-control" type="text" placeholder="Dr.Alex" readonly/>
                                    </div>
                                    <div class="form-group">
                                        <small className='text-bold'>Date</small>
                                        <input class="form-control" type="date" placeholder="Alex B." readonly/>
                                    </div>
                                    <div class="form-group">
                                        <small className='text-bold'>Note</small>
                                        <input class="form-control" type="text" placeholder="Special Note" readonly/>
                                    </div>
                                    <div className='vpd-more-details'>
                                        <p className=' text-bold text-right vpd-more-details-text'
                                            onClick={
                                                () => onOpenLastMedicationDetailsModel()
                                        }>More Details
                                            <i class="p-2 fa fa-caret-right" aria-hidden="true"></i>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='col-sm-4'>
                            <div className='card patient-card'>
                                <div className='row p-2'>
                                    <h4>
                                        Vaccination Details
                                    </h4>
                                    <div className='row'>
                                        <div className='col-sm-10'>
                                            <div className='card'>
                                                <h4>Vaccine 01</h4>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='vpd-more-details'>
                                        <p className=' text-bold text-right vpd-more-details-text'
                                            onClick={
                                                () => onOpenAddNewVaccinationDetailsModel()
                                        }>
                                            <i class="p-2 fa fa-plus-square" aria-hidden="true"></i>
                                            Add New Vaccine Details
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End First Row */}

                    {/* Start Second Row */}
                    <div className='row pt-4'>
                        <div className='col-sm-4'>
                            <div className='card patient-card'>
                                <div className='row p-2'>
                                    <h4>
                                        Allergies Details
                                    </h4>
                                    <div className='vpd-more-details'>
                                        <p className=' text-bold text-right vpd-more-details-text'
                                            onClick={
                                                () => onOpenAlergiesDetailsModel()
                                        }>
                                            <i class="p-2 fa fa-plus-square" aria-hidden="true"></i>
                                            Add New Allergies Details
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='col-sm-4'>
                            <div className='card patient-card'>
                                <div className='row p-2'>
                                    <h4>
                                        Meidcation History
                                    </h4>
                                    <div className='row'>
                                        <div className='col-sm-10'>
                                            <div className='card'>
                                                <h4>Prescription 01</h4>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='row mt-2'>
                                        <div className='col-sm-10'>
                                            <div className='card'>
                                                <h4>Prescription 01</h4>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='vpd-more-details'>
                                        <p className=' text-bold text-right vpd-more-details-text'
                                            onClick={
                                                () => onOpenShowMedicationHistory()
                                        }>More Details
                                            <i class="p-2 fa fa-caret-right" aria-hidden="true"></i>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className='col-sm-4'>
                            <div className='card patient-card'>
                                <div className='row p-2'>
                                    <h4>
                                        Chronic Diseases Condition
                                    </h4>
                                    <div className='row'>
                                        <div className='col-sm-10'>
                                            <div className='card'>
                                                <h4>Prescription 01</h4>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='row'>
                                        <div className='col-sm-10'>
                                            <div className='card'>
                                                <h4>Prescription 01</h4>
                                            </div>
                                        </div>
                                    </div>
                                    <div className='vpd-more-details'>
                                        <p className=' text-bold text-right vpd-more-details-text'>More Details
                                            <i class="p-2 fa fa-caret-right" aria-hidden="true"></i>
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )

}
export default ViewPatientDetails;
