import './App.css';
import React from 'react';
import {BrowserRouter as Router, Route, Switch} from "react-router-dom";
import Index from "./Components/index";
import MedicalDocumentScannerComponent from "./Components/medicalDocumentScannerComponent";
import EntityExtractionComponent from "./Components/entityExtractionComponent";
import ViewPatientDetails from './Components/viewPatientDetails';

function App() {
    return (
        <Router>
            <div>
                <Route path="/" exact
                    component={Index}></Route>
                <Route path="/docScanner"
                    component={MedicalDocumentScannerComponent}></Route>
                <Route path="/entityExtraction"
                    component={EntityExtractionComponent}></Route>
                <Route path="/viewPatientDetails"
                    component={ViewPatientDetails}></Route>
            </div>
        </Router>
    );
}

export default App;
