import React, {Component} from 'react';
import document from "../images/2.jpeg";
import Header from "./header";
import Navbar from "./navbar";

class MedicalDocumentScannerComponent extends Component {
    render() {
        return (
            <div>
                <Header></Header>
                <br/>
                <div className="container-fluid" style={{backgroundColor:"#E5E5E5",paddingTop:"20px"}}>
                    <div className="container">
                        <h2>Medical Document Scanner</h2>
                        <br/>
                        <div className="container bg-light">
                            <div className="row">
                                <div className="col-sm-2" style={{alignItems:"center",paddingLeft:"30px"}}>
                                    <br/>
                                    <i className="fa fa-book" aria-hidden="true" style={{fontSize:"40px"}}></i>
                                </div>
                                <div className="col-sm-10" style={{paddingTop:"20px"}}>
                                    <h4>Scan the Medical Documents</h4>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-sm-2"></div>
                                <div className="col-sm-10">
                                    <p>Medical Document Scanner is a free online OCR(Optical Character Recognition) service and can analyze the text in the image you upload. The function of OCR is to help digitize documents in a timely manner and fashion.
                                        Instead of retyping files or manually typing out text within image files, you can use OCR to automatically scan and recognize text within your image or your scanned document.
                                    </p>
                                </div>
                            </div>
                            <br/>
                        </div>
                        <br/>
                        <div className="container bg-light">
                            <div className="row" style={{paddingTop:"20px"}}>
                                <h6>Select an Image</h6>
                            </div>
                            <br/>
                            <div className="row">
                                <div className="col-sm-10">
                                    <div className="mb-3">
                                        <input className="form-control" type="file" id="formFileMultiple" multiple/>
                                    </div>
                                </div>
                                <div className="col-sm-2">
                                    <button type="button" className="btn btn-primary">Upload</button>
                                </div>
                            </div>
                            <div className="row">
                                <div className="col-sm-1"></div>
                                <div className="col-sm-9">
                                    <div className="d-grid gap-2">
                                        <button className="btn btn-primary" type="button">Wrap Document and Extract Text</button>
                                    </div>
                                </div>
                                <div className="col-sm-2"></div>
                            </div>
                            <br/><br/><br/>
                        </div>
                        <div className="container-fluid bg-light">
                            <div className="row" style={{alignItems:"center"}}>
                                <center>
                                    <img src={document} style={{width:"50%",height:"50%"}}/>
                                </center>

                            </div>
                        </div>
                    </div>
                    <br/>
                    <div className="container bg-light">
                        <div className="container overflow-hidden">
                            <br/>
                            <div className="row gy-5">
                                <div className="col-4">
                                    <div className="p-3 border bg-light">
                                        <h3>1</h3>
                                        <h6>Choose File</h6>
                                        <h5>Choose an Image file from the device</h5>
                                        <br/>
                                    </div>
                                </div>
                                <div className="col-4">
                                    <div className="p-3 border bg-light">
                                        <h3>2</h3>
                                        <h6>Upload Image</h6>
                                        <h5>Upload the selected Image for scanning</h5>
                                        <br/>
                                    </div>
                                </div>
                                <div className="col-4">
                                    <div className="p-3 border bg-light">
                                        <h3>3</h3>
                                        <h6>Wrap Document and Extract Text</h6>
                                        <h5>Click on wrap document button to extract text</h5>
                                    </div>
                                </div>
                            </div>
                            <br/>
                        </div>
                    </div>
                    <br/>
                </div>
            </div>
        );
    }
}

export default MedicalDocumentScannerComponent;
